.Navbar {
  display: flex;
  justify-content: space-around;
  padding: 12px;
  box-shadow: 0 1px 3px -2px black;
  align-items: center;
}
.nav-logo {
  display: flex;
  align-items: center;
  gap: 10px;
}
.nav-logo p {
  color: #171717;
  font-size: 30px;
  font-weight: 600;
}
.Nav-menu {
  display: flex;
  align-items: center;
  list-style: none;
  color: #616161;
  gap: 50px;
  font-size: 22px;
}
.Nav-menu li {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 3px;
  cursor: pointer;
}
.Nav-menu a {
  color: black;
  font-family: Poppins;
  font-weight: 600;
}

.Nav-menu hr {
  width: 80%;
  border: none;
  height: 3px;
  border-radius: 10px;
  background-color: rgb(137, 49, 225);
}
.Nav-cart {
  display: flex;
  align-items: center;
  justify-content: center;
}
.Nav-cart button {
  width: 157px;
  height: 40px;
  outline: none;
  border: 2px solid gray;
  border: 75px;
  color: #ff2222;
  font-size: 23px;
  cursor: pointer;
  font-weight: 500;
  background: white;
}
.Nav-cart button {
  font-size: 24px;
  font-weight: 597;
  border: 3px solid gray;
  border-radius: 35px;

  width: 120px;
  height: 45px;
  padding: 2px;
  color: gray;
  text-align: center;
}
.Nav-cart button :hover {
  background-color: aliceblue;
}
.nav-cart-count {
  top: 13px;
  right: 86px;
  position: absolute;
  background-color: red;
  color: white;
  height: 22px;
  width: 21px;
  font-weight: 600;
  padding: 5px;
  border-radius: 29px;
  text-align: center;
}
@media (max-width: 1280px) {
  .Navbar {
    padding: 12px 50px;
  }
  .nav-logo img {
    width: 40px;
  }
  .nav-logo p {
    font-size: 25px;
  }
  .Nav-menu {
    gap: 30px;
    font-size: 16px;
  }
  .Nav-cart {
    gap: 30px;
  }
  .Nav-cart button {
    width: 120px;
    height: 45px;
    font-size: 16px;
  }
  .nav-cart-count {
    margin-left: -40px;
    font-size: 12px;
  }
  .Nav-cart {
    gap: 30px;
    position: relative;
  }

  .nav-cart-count {
    top: -5px;
    right: -19px;
    position: absolute;
    background-color: red;
    color: white;
    height: 22px;
    width: 21px;
    font-weight: 600;
    padding: 5px;
    border-radius: 29px;
    text-align: center;
  }
}
@media (max-width: 1024px) {
  .navbar {
    padding: 12px 30px;
  }
  .Nav-menu {
    gap: 25px;
    font-size: 14px;
  }
  .Nav-cart button {
    width: 80px;
    height: 35px;
    font-size: 14px;
  }
  .Nav-cart img {
    width: 30px;
  }
  .nav-cart-count {
    height: 18px;
    width: 18px;
  }
  .Nav-cart {
    gap: 30px;
    position: relative;
  }

  .nav-cart-count {
    top: -5px;
    right: -19px;
    position: absolute;
    background-color: red;
    color: white;
    height: 22px;
    width: 21px;
    font-weight: 600;
    padding: 5px;
    border-radius: 29px;
    text-align: center;
  }
  .navdrop {
    display: none;
  }
  .Nav-cart button {
    font-size: 24px;
    font-weight: 500;
  }
  .Nav-menu li {
    font-size: 22px;
    gap: 6px;
  }
}
.Nav-cart {
  gap: 30px;
  position: relative;
}

.nav-cart-count {
  top: -5px;
  right: -19px;
  position: absolute;
  background-color: red;
  color: white;
  height: 22px;
  width: 21px;
  font-weight: 600;
  padding: 5px;
  border-radius: 29px;
  text-align: center;
}
.navdrop {
  display: none;
}
@media (max-width: 800px) {
  .navbar {
    padding: 10px 0px;
  }
  .navdrop {
    display: block;
    width: 30px;
    transform: rotate(-90deg);
    transition: 0.5s;
  }
  .Nav-menu {
    display: none;
    height: 80px;
    width: 100%;
    position: absolute;
    background-color: white;
    justify-content: center;
    top: 50px;
  }
  .nav-menu-visible {
    display: flex;
  }
  .navdrop .open {
    transform: rotate(90deg);
  }
  .Nav-cart button {
    font-size: 23px;
  }
  .Nav-menu li {
    font-size: 15px;
    gap: 6px;
  }
}
@media (max-width: 500px) {
  .nav-logo p {
    font-size: 19px;
  }
  .navbar {
    padding: 8px 0px;
  }
  .nav-logo {
    transform: scale(0.8);
  }
  .Nav-menu {
    height: 70px;
    top: 50px;
  }
  .Nav-cart {
    transform: scale(0.8);
  }
  .Nav-cart button {
    font-size: 20px;
  }
  .Nav-menu li {
    font-size: 15px;
    gap: 6px;
  }
}
.nav-cart-count {
  top: 2px;
  right: 1px;
  position: absolute;
  background-color: red;
  color: white;
  height: 13px;
  width: 13px;
  font-weight: 600;
  padding: 3px;
  border-radius: 29px;
  text-align: center;
  font-size: 10px;
}

@media (max-width: 500px) {
  .Navbar {
    padding: 12px 10px;
  }
}
.nav-cart-count {
  overflow: hidden;
}
