.New-collection {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
  margin-bottom: 100px;
}
.New-collection h1 {
  color: black;
  font-size: 40px;
  font-weight: 700;
  margin-top: 5vh;
}
.New-collection hr {
  width: 200px;
  height: 6.3px;
  border-radius: 10px;
  background: #252525;
}
.collection {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  margin-top: 50px;
  gap: 30px;
}

@media (max-width: 1280px) {
  .New-collection {
    height: 70vh;
  }
  .New-collection h1 {
    font-size: 40px;
  }
  .New-collection hr {
    width: 160px;
    height: 4px;
  }
  .collection {
    gap: 20px;
    margin-top: 30px;
  }
}

@media (max-width: 1280px) {
  .New-collection h1 {
    font-size: 40px;
  }
  .New-collection hr {
    width: 160px;
    height: 4px;
  }
  .collection {
    gap: 20px;
    margin-top: 30px;
  }
}

@media (max-width: 1024px) {
  .New-collection h1 {
    font-size: 30px;
  }
  .New-collection hr {
    width: 120px;
    height: 3px;
  }
  .collection {
    gap: 15px;
    margin-top: 20px;
  }
}

@media (max-width: 800px) {
  .New-collection {
    height: 60vh;
  }
  .New-collection h1 {
    font-size: 30px;
  }
  .New-collection hr {
    width: 120px;
