  }
}

@media (max-width: 500px) {
  .collection {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
  }
}
@media (max-width: 800px) {
  .New-collection {
    height: fit-content;
  }
}
@media (max-width: 500px) {
  .New-collection {
