.breadcrumb {
  display: flex;
  align-items: center;
  gap: 5px;
  font-family: Arial, sans-serif;
  color: black;
  margin-top: 2vh;
  padding: 0px 30px;
  margin-bottom: 3vh;
}

.breadcrumb span {
  text-transform: uppercase;
}
.breadcrumb img {
  background-color: #bdb7b7;
  color: cyan;
  padding: 3px 4px;
  border-radius: 9px;
}
@media (max-width: 1280px) {
  .breadcrumb {
    margin: 30px 50px;
    font-size: 14px;
  }
}

@media (max-width: 1024px) {
  .breadcrumb {
    margin: 30px 30px;
    font-size: 13px;
  }
}

@media (max-width: 800px) {
  .breadcrumb {
    margin: 30px 10px;
    font-size: 12px;
  }
}

@media (max-width: 500px) {
  .breadcrumb {
    font-size: 11px;
  }
}
