.LoginSignup {
  width: 100%;
  height: 100vh;
  background-color: #fce3fe;
  padding-top: 100px;
  margin-bottom: 5vh;
}

.LoginSignupContainer {
  width: 580px;
  height: 600px;
  background: white;
  margin: auto;
  padding: 40px 60px;
}

.LoginSignupContainer h1 {
  margin: 20px 0px;
}

.LoginSignupFields {
  display: flex;
  flex-direction: column;
  margin-top: 30px;
  gap: 29px;
}

.LoginSignupFields input {
  height: 72px;
  width: 100%;
  padding-left: 20px;
  border: 1px solid #c9c9c9;
  outline: none;
  color: #5c5c5c;
  font-size: 22px;
  font-weight: 500;
  position: relative;
}

.LoginSignupFields label {
  position: absolute;
  top: 0;
  left: 0;
  font-size: 1rem;
  transition: 0.2s;
  color: #9b9b9b;
}

.LoginSignupFields input:focus,
.LoginSignupFields input:not(:placeholder-shown) + label {
  transform: translateY(-5px);
  font-size: 20px;
  font-weight: 500;
}

.LoginSignupFields input:focus {
  box-shadow: 0 0 10px #719ece;
}

.LoginSignupContainer button {
  width: 580px;
  height: 72px;
  color: white;
  background: #ff4141;
  margin-top: 30px;
  border: none;
  font-size: 24px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

.LoginSignupContainer button:hover {
  background: #ff6363;
}

.LoginSignupContainer button:active {
  background: #ff2323;
}

.LoginSignupLogin {
  margin-top: 20px;
  color: #5c5c5c;
  font-size: 18px;
  font-weight: 500;
}

.LoginSignupLogin span {
  color: #ff4141;
  font-weight: 600;
  cursor: pointer;
}

.LoginSignupAgree {
  display: flex;
  align-items: center;
  margin-top: 25px;
  gap: 20px;
  color: #5c5c5c;
}
@media (max-width: 1280px) {
  .LoginSignup {
    padding-top: 50px;
  }
  .LoginSignupContainer {
    width: auto;
    max-height: 532px;
    max-width: 500px;
  }
  .LoginSignupContainer h1 {
    margin: 10px 0px;
  }
  .LoginSignupFields {
    gap: 20px;
    margin-top: 20px;
  }
  .LoginSignupFields input {
    height: 65px;
    width: 93%;
  }
  .LoginSignupContainer button {
    width: 100%;
  }
  .LoginSignupLogin {
    font-size: 16px;
  }
  .LoginSignupAgree {
    font-size: 14px;
    gap: 10px;
  }
}
@media (max-width: 800px) {
  .LoginSignupContainer {
    padding: 20px 30px;
  }
  .LoginSignupContainer h1 {
    font-size: 24px;
  }
  .LoginSignupFields {
    margin-top: 20px;
  }
  .LoginSignupFields input {
    height: 50px;
  }
  .LoginSignupContainer button {
    height: 50px;
    font-size: 16px;
  }
}
