.RealatedProduct {
  width: 100%;
  background-color: white;
  padding: 20px;
}

.RealatedProduct h1 {
  text-align: center;
  margin-bottom: 20px;
}

.RealatedProduct hr {
  margin-bottom: 20px;
}

.RealatedProductitem {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.Item {
  width: 23%; /* Adjust this value to control the number of items per row */
  border: 1px solid #ddd;
  margin-bottom: 20px;
  padding: 10px;
}

.Item img {
  width: 100%;
  height: auto;
}

.Item .name {
  font-size: 16px;
  margin: 10px 0;
}

.Item .new_price {
  color: red;
  font-size: 20px;
  font-weight: bold;
}

.Item .old_price {
  text-decoration: line-through;
  color: #999;
  font-size: 16px;
}
@media (max-width: 1280px) {
  .Item {
    width: 21%; /* Adjust this value to control the number of items per row */
  }
  .RealatedProduct h1 {
    font-size: 25px;
  }
}

@media (max-width: 1040px) {
  .Item {
    width: 19%; /* Adjust this value to control the number of items per row */
  }
  .RealatedProduct h1 {
    font-size: 23px;
  }
}

@media (max-width: 800px) {
  .Item {
    width: 47%; /* Adjust this value to control the number of items per row */
  }
  .RealatedProduct h1 {
    font-size: 21px;
  }
  .RealatedProductitem {
    justify-content: center;
    gap: 8vw;
  }
}

@media (max-width: 500px) {
  .Item {
    width: 47%; /* Adjust this value to control the number of items per row */
  }
  .RealatedProduct h1 {
    font-size: 25px;
  }
  .RealatedProductitem {
    justify-content: center;
    gap: 5vw;
  }
}
