.product-card {
  display: flex;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  font-family: Arial, sans-serif;
}

.product-card__image-section {
  flex: 1;
}

.product-card__thumbnails {
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
}

.product-card__thumbnails img {
  width: 60px;
  height: 60px;
  object-fit: cover;
  border-radius: 5px;
}

.product-card__main-image img {
  width: 100%;
  height: auto;
  border-radius: 10px;
}

.product-card__details {
  flex: 1;
  margin-left: 20px;
}

.product-card__name {
  font-size: 24px;
  color: #333;
  font-weight: 600;
}

.product-card__rating {
  display: flex;
  align-items: center;
  gap: 5px;
  margin: 10px 0;
}

.product-card__rating img {
  width: 20px;
  height: 20px;
}

.product-card__prices {
  display: flex;
  gap: 10px;
  margin-bottom: 10px;
}

.product-card__new-price {
  color: #e60023;
  font-size: 22px;
  font-weight: bold;
}

.product-card__old-price {
  color: #999;
  text-decoration: line-through;
}

.product-card__description {
  margin-bottom: 20px;
  color: #666;
  line-height: 1.5;
}

.product-card__size-selector h1 {
  font-size: 18px;
  margin-bottom: 10px;
  color: #333;
}

.product-card__sizes {
  display: flex;
  gap: 10px;
}

.product-card__sizes div {
  padding: 5px 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  cursor: pointer;
}

.product-card__add-to-cart {
  display: inline-block;
  background-color: #e60023;
  color: #fff;
  padding: 10px 20px;
  border-radius: 5px;
  text-transform: uppercase;
  margin-top: 20px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.product-card__add-to-cart:hover {
  background-color: #c5001a;
}

.product-card__category,
.product-card__tags {
  margin-top: 10px;
  color: #666;
}

@media (max-width: 1280px) {
  .product-card {
    margin: 0px 60px;
  }
  .product-card__images {
    gap: 10px;
  }
  .product-card__thumbnails {
    gap: 10px;
  }
  .product-card__thumbnails img {
    height: 120px;
  }
  .product-card__main-image {
    width: auto;
    height: 510px;
  }
  .product-card__details {
    margin: 0px 30px;
  }
  .product-card__details h1 {
    font-size: 22px;
  }
  .product-card__rating {
    gap: 3px;
    font-size: 13px;
  }
  .product-card__prices {
    margin: 10px 0px;
    font-size: 18px;
  }
  .product-card__description {
    font-size: 13px;
  }
  .product-card__size-selector {
    padding: 14px 20px;
  }
  .product-card__add-to-cart {
    width: 150px;
    padding: 15px 0px;
    margin-bottom: 20px;
  }

  .product-card__category {
    margin-top: 5px;
    font-size: 14px;
  }
  .product-card__tags {
    margin-top: 5px;
    font-size: 14px;
  }
  .product-card__main-image img {
    overflow-y: hidden;
    height: fit-content;
  }
}
@media (max-width: 1024px) {
  .product-card {
    margin: 0px 30px;
  }
  .product-card__images {
    gap: 5px;
  }

  .product-card__thumbnails img {
    height: 80px;
  }
  .product-card__main-image {
    height: 350px;
  }

  .product-card__details h1 {
    font-size: 18px;
  }
  .product-card__rating img {
    width: 15px;
  }

  .product-card__description {
    font-size: 12px;
  }
  .product-card__size-selector {
    padding: 10px 16x;
    font-size: 12px;
  }
  .product-card__add-to-cart {
    width: 120px;
    padding: 10px 0px;
    margin-bottom: 10px;
    font-size: 14px;
  }

  .product-card__category {
    font-size: 12px;
  }
  .product-card__tags {
    font-size: 12px;
  }
  .product-card__main-image img {
    overflow-y: hidden;
    height: fit-content;
    width: 290px;
  }
}
@media (max-width: 800px) {
  .product-card {
    margin: 0px 10px;
  }
  .product-card__images {
    gap: 0px;
    flex-direction: column-reverse;
    align-items: center;
  }
  .product-card__thumbnails {
    flex-direction: row;
    gap: 6px;
  }
  .product-card__thumbnails img {
    height: 70px;
  }
  .product-card__main-image {
    height: 300px;
  }

  .product-card__details h1 {
    font-size: 14px;
  }
  .product-card__rating img {
    width: 13px;
  }

  .product-card__description {
    font-size: 10px;
  }
  .product-card__size-selector h1 {
    margin-top: 10px;
  }
  .product-card__size-selector {
    padding: 4px 11px;
  }
  .product-card__add-to-cart {
    width: 100px;

    font-size: 12px;
  }

  .product-card__category {
    font-size: 12px;
  }
  .product-card__tags {
    font-size: 12px;
  }
  .product-card__main-image img {
    overflow-y: hidden;
    height: fit-content;
    width: 245px;
  }
}
@media (max-width: 500px) {
  .product-card {
    flex-direction: column;
  }
  .product-card__images {
    gap: 10px;
    flex-direction: row;
    margin: auto;
  }
  .product-card__thumbnails {
    gap: 8px;
  }
  .product-card__thumbnails img {
    height: 75px;
  }
  .product-card__main-image {
    height: 330px;
  }
  .product-card__details {
    margin: 5px;
  }
  .product-card__details h1 {
    margin-top: 15px;
    font-size: 20px;
    font-weight: 500;
  }

  .product-card__rating img {
    width: 15px;
  }
  .product-card__size-selector {
    gap: 10px;
    margin: 20px 0px;
  }
  .product-card__sizes div {
    padding: 10px 16px;
    font-size: 14px;
  }
  .product-card__add-to-cart {
    width: 130px;
    font-size: 15px;
    padding: 12px 0px;
  }

  .product-card__category {
    font-size: 16px;
  }
  .product-card__tags {
    font-size: 16px;
  }
  .product-card__main-image img {
    overflow-y: hidden;
    height: fit-content;
    width: 266px;
    height: 294px;
    background-size: cover;
  }
}
