.Popular {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
  height: 130vh;
}
.Popular h1 {
  color: black;
  font-size: 40px;
  font-weight: 700;
  margin-top: 5vh;
  overflow-y: hidden;
}
.Popular hr {
  width: 200px;
  height: 6.3px;
  border-radius: 10px;
  background: #252525;
  overflow-y: hidden;
}
.popular-item {
  margin-top: 50px;
  display: flex;
  gap: 30px;
  overflow-y: hidden;
}

@media (max-width: 1280px) {
  .Popular {
    height: 70vh;
  }
  .Popular h1 {
    font-size: 40px;
  }
  .Popular hr {
    width: 160px;
    height: 4px;
  }
  .popular-item {
    gap: 20px;
    margin-top: 30px;
  }
}

@media (max-width: 1024px) {
  .Popular {
    height: 60vh;
  }
  .Popular h1 {
    font-size: 30px;
  }
  .Popular hr {
    width: 120px;
    height: 4px;
  }
  .popular-item {
    gap: 15px;
    margin-top: 20px;
  }
}

@media (max-width: 800px) {
  .Popular {
    height: 73vh;
    gap: 6px;
  }
  .Popular h1 {
    font-size: 20px;
  }
  .Popular hr {
    width: 100px;
  }
  .popular-item {
    gap: 5px;
  }

  .popular-item {
    gap: 5px;
    height: auto;
    height: 55vh;

    gap: 20px;
  }
  .Popular {
    height: 67vh;
    gap: 6px;
  }
}

@media (max-width: 500px) {
  .Popular {
    height: 124vh;
  }

  .popular-item {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
  }
}
@media (max-width: 500px) {
  .popular-item {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    height: 94vh;
  }
}
