.NewsLetter {
  height: 40vh;
  width: 65%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: auto;
  padding: 0px 140px;
  margin-bottom: 200px;
  margin-top: 5vh;
  background-color: linear-gradient(180deg #ee00ff 100%);
  gap: 30px;
}
.NewsLetter h1 {
  color: #454545;
  font-size: 45px;
  font-weight: 600;
}
.NewsLetter p {
  color: #454545;
  font-size: 20px;
}
.NewsLetter div {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: white;
  width: 730px;
  height: 70px;
  border-radius: 80px;
  border: 1px solid rgb(195, 205, 166);
}
.NewsLetter input {
  width: 500px;
  margin-left: 30px;
  border: none;
  outline: none;
  color: #545454;
  font-family: Poppins, "Courier New", Courier, monospace;
  font-size: 18px;
}
.button {
  width: 210px;
  height: 70px;
  border-radius: 80px;
  background: black;
  color: white;
  cursor: pointer;
  font-size: 16px;
}
@media (max-width: 1280px) {
  .NewsLetter {
    padding: 0px 80px;
    margin-bottom: 120px;
    gap: 20px;
  }
  .NewsLetter h1 {
    font-size: 36px;
  }
  .NewsLetter p {
    font-size: 16px;
  }
  .NewsLetter div {
    width: 600px;
    height: 60px;
  }
  .NewsLetter div input {
    width: 400px;
  }
  .NewsLetter div button {
    width: 160px;
    height: 60px;
  }
}
@media (max-width: 1024px) {
  .NewsLetter {
    margin-bottom: 60px;
  }
  .NewsLetter h1 {
    font-size: 30px;
  }
  .NewsLetter p {
    font-size: 14px;
  }
  .NewsLetter div {
    width: 500px;
    height: 50px;
  }
  .NewsLetter div input {
    width: 300px;
    font-size: 14px;
  }
  .NewsLetter div button {
    width: 120px;
    height: 50px;
    font-size: 14px;
  }
}
@media (max-width: 800px) {
  .NewsLetter {
    margin-bottom: 30px;
    height: 35vh;
  }
  .NewsLetter h1 {
    font-size: 18px;
  }
  .NewsLetter p {
    font-size: 12px;
  }
  .NewsLetter div {
    width: 400px;
    height: 40px;
  }
  .NewsLetter div input {
    width: 200px;
    font-size: 13px;
  }
  .NewsLetter div button {
    width: 100px;
    height: 40px;
    font-size: 13px;
  }
}
@media (max-width: 500px) {
  .NewsLetter {
    width: 100%;
    padding: 0;
    gap: 15px;
    height: 25vh;
  }
  .NewsLetter h1 {
    font-size: 16px;
  }
  .NewsLetter p {
    font-size: 12px;
    max-width: 200px;
    text-align: center;
  }
  .NewsLetter div {
    width: 260px;
  }

  .NewsLetter div input {
    width: 130px;
  }
}
