.cartItems {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20 0px;
  padding-left: 80px;
  padding-right: 80px;
  font-size: 17px;
  overflow-x: hidden;
}

.cartitemformatname,
.cartitemformat {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 5px;
  align-items: center;
  width: 100%;
  padding: 10px;
}

.cartitemformatname p {
  font-weight: bold;
  text-transform: uppercase;
}

.carticonproducticon {
  width: 50px;
  height: 50px;
}

.cartitemquantity {
  background-color: transparent;
  text-align: left;
  border: none;
  padding: 5px 10px;
  cursor: pointer;
}

hr {
  width: 100%;
  border: none;
  border-top: 1px solid #ddd;
}

.catitemsdown {
  display: flex;
  margin: 100px 0px;
  width: 100%;
}

.cartitemtotal {
  flex: 1;
  display: flex;
  flex-direction: column;
  margin-right: 200px;
  gap: 40px;
}

.cartitemtotal h1,
.cartitemtotalitem h3 {
  color: #000;
}

.cartitemtotalitem {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 0;
}

.cartitemtotalitems p,
.cartitemtotalitem p,
.cartitemtotalitem h3 {
  margin: 0;
}

.cartitemtotal hr {
  border: none;
  border-top: 1px solid #ddd;
}

.cartitemtotal button {
  background-color: red;
  color: #fff;
  padding: 10px 20px;
  border: none;
  cursor: pointer;
  margin-top: 20px;
  height: 58px;
  width: 262px;
  font-size: 21px;
  font-weight: 500;
}

.cartitempromocode {
  flex: 1 1;
  font-size: 16px;
  font-weight: 500;
  width: 504px;
  margin-top: 15px;
  height: 60px;
  font-size: 18px;
}

.cartitempromocode p {
  margin-bottom: 10px;
  color: #555;
}

.cartitempromocode input {
  border: none;
  outline: none;
  background: #f1f1f1;
  font-size: 17px;
  width: 330px;
  height: 61px;
  padding: 0px 4px;
}

.cartitempromocode button {
  background-color: #000;
  color: #fff;
  padding: 5px 10px;
  border: none;
  cursor: pointer;
  margin-left: 10px;
  width: 170px;
  height: 58px;
  font-size: 17px;
  justify-self: unset;
  margin-top: 3vh;
  margin-right: -1vw;
}

.cartitempromocode input::placeholder {
  margin-right: -32vw;
  color: black;
  padding: -33px;
  font-size: 20px;
  position: absolute;
  left: 30px;
  right: 30px;
}
.cartitemtotalitems {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 1280px) {
  .cartItems {
    margin: 60px 50px;
  }
  .cartitemformat {
    grid-template-columns: 1fr 3fr 0.5fr 0.5fr 0.5fr 0.5fr;
    gap: 20px;
    padding: 15px 0px;
    font-size: 15px;
  }
  .cartitemformatname {
    grid-template-columns: 1fr 2.5fr 0.5fr 0.5fr 0.5fr 0.5fr;
    font-size: 15px;
  }
  .cartitemquantity {
    width: 40px;
    height: 30px;
  }
  .catitemsdown {
    margin: 80px 0px;
    flex-direction: column;
    gap: 80px;
  }
  .cartitemtotal {
    margin: 0;
  }
  .cartitemtotal button {
    max-width: 200px;
    height: 45px;
    font-size: 13px;
  }
  .carticonproducticon {
    height: 50px;
  }
  .cartitempromocode {
    height: fit-content;
  }
  .cartitempromocode input {
    width: 100%;
  }
  .cartitempromocode button {
    width: 120px;
    margin-left: -120px;
  }
}
@media (min-width: 600px) and (max-width: 800px) {
  .cartitempromocode button {
    width: 120px;
    margin-left: -130px;
  }
}

@media (min-width: 500px) and (max-width: 750px) {
  .cartitemformatname {
    display: none;
    grid-template-columns: 0.5fr 3fr 0.5fr;
    gap: 10px;
  }

  .cartitemformat {
    display: grid;
  }
  .cartItems {
    margin: 60px 0px;
    padding: 0px 10px;
  }
  .cartitempromocode button {
    width: 120px;
    margin-left: -130px;
  }
  .cartitempromocode {
    width: 420px;
  }
}
