.ShopCategory {
  /* Add styles for the ShopCategory container */
  width: 100%;
  padding: 20px;
  box-sizing: border-box;
}

.shopcategorybanner {
  /* Add styles for the banner image */
  display: block;
  width: 100%;
  height: auto;
  margin-bottom: 20px;
}

.ShopCategory-indexsort {
  /* Add styles for the index sort section */
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0;
  border-bottom: 1px solid #ccc;
}

.ShopCategory-indexsort p {
  /* Add styles for the paragraph in the index sort section */
  margin: 0;
  font-size: 16px;
  color: #333;
}

.ShopCategory-indexsort p span {
  /* Add styles for the span in the paragraph */
  font-weight: bold;
}

.ShopCategory-sort {
  /* Add styles for the sort section */
  display: flex;
  align-items: center;
  padding: 5px 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.ShopCategory-sort img {
  /* Add styles for the dropdown icon in the sort section */
  margin-left: 5px;
  width: 12px;
  height: 12px;
}

.ShopCategory-products {
  /* Add styles for the products section */
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  margin-top: 20px;
}
.shopcategoryloadmore {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 150px auto;
  width: 233px;
  height: 69px;
  border-radius: 75px;
  background: #ededed;
  color: #787878;
  font-size: 19px;
  font-weight: 500;
}
@media (max-width: 1280px) {
  .shopcategorybanner {
    margin: 20px auto;
    width: 90%;
  }
  .ShopCategory-indexsort {
    width: 90%;
    margin: auto;
  }
  .ShopCategory-sort {
    padding: 5px 15px;
    font-size: 12px;
  }
  .ShopCategory-indexsort p {
    font-size: 12px;
  }
  .ShopCategory-products {
    width: 90%;
    margin: 10px auto;
    row-gap: 40px;
  }
  .ShopCategory-products .item {
    margin: auto;
  }
  .shopcategoryloadmore {
    margin: 100px auto;
    width: 200px;
    height: 60px;
    font-size: 16px;
  }
}
@media (max-width: 800px) {
  .shopcategorybanner {
    width: 95%;
  }
  .ShopCategory-products {
    width: 95%;
  }
  .shopcategoryloadmore {
    margin: 60px auto;
    width: 150px;
    height: 40px;
    font-size: 13px;
  }
}

@media (max-width: 500px) {
  .ShopCategory-products {
    grid-template-columns: 1fr 1fr;
  }
}
