.offers {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 65%;
  height: 60%;
  margin: auto;
  padding: 0px 140px;
  background: linear-gradient(180deg, #fde1ff 0%, #e1ffea22 60%);
  margin-top: 6vh;
}
.offersleft {
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: center;
}
.offersleft h1 {
  color: #171717;
  font-size: 70px;
  font-weight: 600;
}
.offersleft p {
  color: #171717;
  font-size: 22px;
  font-weight: 600;
}

.offersleft button {
  width: 282px;
  height: 70px;
  border-radius: 35px;
  background: #ff4141;
  border: none;
  color: white;
  font-size: 22px;
  font-weight: 500;
  margin-top: 30px;
  cursor: pointer;
  transition: 0.5s all;
}

.offersleft button:hover {
  width: 286px;
  height: 75px;
  background: #ff1c1cb6;

  color: rgb(232, 226, 226);
  font-size: 22px;
}
.offersright {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-top: 50px;
}

@media (max-width: 1280px) {
  .offers {
    padding: 0px 80px;
    margin-bottom: 120px;
  }
  .offersleft h1 {
    font-size: 40px;
  }
  .offersleft p {
    font-size: 18px;
  }
  .offersright img {
    width: 300px;
  }
  .offersleft button {
    width: 220px;
    height: 50px;
    font-size: 18px;
  }
  .offersright {
    padding: 30px;
  }
}

@media (max-width: 1024px) {
  .offers {
    padding: 0px 60px;
    margin-bottom: 80px;
    height: 40vh;
  }
  .offersleft h1 {
    font-size: 30px;
  }
  .offersleft p {
    font-size: 16px;
  }
  .offersright img {
    width: 200px;
  }
  .offersleft button {
    width: 160px;
    height: 40px;
    font-size: 16px;
  }
  .offersright {
    padding: 10px;
  }
}

@media (max-width: 800px) {
  .offers {
    margin-bottom: 60px;
  }
  .offersleft h1 {
    font-size: 22px;
  }
  .offersleft p {
    font-size: 13px;
  }
  .offersright img {
    width: 180px;
  }
  .offersleft button {
    width: 140px;
    height: 30px;
    font-size: 12px;
  }
}

@media (max-width: 500px) {
  .offers {
    height: 25vh;
    margin-bottom: 50px;
  }
  .offersleft h1 {
    font-size: 16px;
  }
  .offersleft p {
    font-size: 10px;
  }
  .offersright img {
    width: 120px;
  }
  .offersleft button {
    width: 120px;
    height: 25px;
    font-size: 10px;
    margin-top: 12px;
  }
}
@media (max-width: 500px) {
  .offers {
    height: 25vh;
    margin-bottom: 50px;
    overflow-y: hidden;
    top: -50vh;
    margin-top: -21vh;
  }
}
@media (max-width: 500px) {
  .offers {
    height: 25vh;
    margin-bottom: 50px;
    overflow-y: hidden;
    top: -50vh;
    margin-top: -10vh;
  }
}
.offers {
  overflow-y: hidden;
}
