.item {
  width: 320px;
  margin-bottom: 5vh;
}

.item p {
  margin: 6px 0px;
  overflow-y: hidden;
}
.prices {
  display: flex;
  gap: 20px;
  overflow-y: hidden;
}
.prices-new-price {
  color: #374151;
  font-size: 18px;
  font-weight: 600;
  overflow-y: hidden;
}
.prices-old-price {
  color: #8c8c8c;
  overflow-y: hidden;
  font-size: 18px;
  font-weight: 500;
  text-decoration: line-through;
}

.item:hover {
  transform: scale(1.05);
  overflow-y: hidden;
  transition: 0.6s;
}
@media (max-width: 1280px) {
  .item {
    width: 220px;
    font-size: 14px;
  }
  .item img {
    width: 220px;
  }
  .prices-old-price {
    font-size: 14px;
  }
}
@media (max-width: 1024px) {
  .item {
    width: 170px;
    font-size: 13px;
  }
  .item img {
    width: 170px;
  }
  .prices-old-price {
    font-size: 13px;
  }
}
@media (max-width: 800px) {
  .item {
    width: 170px;
    font-size: 13px;
  }
  .item img {
    width: 170px;
  }
  .prices-old-price {
    font-size: 12px;
  }
}
@media (max-width: 500px) {
  .item {
    width: 160px;
  }
  .item img {
    width: 160px;
  }
  .item {
    width: 160px;
    height: fit-content;
  }
}
