.DescriptionBox {
  width: 100%;
  padding: 20px;
  box-sizing: border-box;
  background-color: #f8f8f8;
}

.DescriptionBoxNavigator {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.DescriptionBoxnavbox,
.DescriptionBoxnavboxfade {
  padding: 10px;
  border: 1px solid #ddd;
  cursor: pointer;
  text-align: center;
  font-size: 18px;
}

.DescriptionBoxdescription {
  line-height: 1.6;
  margin: 20px;
  font-size: 18px;
  text-align: justify;
}
@media (max-width: 1280px) {
  .DescriptionBox {
    margin: 60px;
  }
}
@media (max-width: 800px) {
  .DescriptionBox {
    margin: 60px 20px;
  }
  .DescriptionBoxdescription {
    font-size: 14px;
  }
}
